body {
  margin: 0;
  background-color: #444;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading-1{
  color: white;
}

.container {
  background-color: #333; 
  color: #fff; 
}

.feature {
  background-color: #444; 
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); 
  transition: transform 0.3s ease-in-out;
}

.feature:hover {
  transform: translateY(-10px); 
}

.feature-icon {
  padding: 10px;
  background-color: #f0f0f0; 
  border-radius: 50%;
  display: inline-block;
  margin-bottom: 15px;
}

.icon-link {
  text-decoration: none;
  color: #0d6efd;
}

.icon-link:hover {
  color: #0056b3; 
}


.signin-container {
  width: 400px; 
  margin: 100px auto; 
  text-align: center; 
  padding: 20px; 
  border: 1px solid #ccc; 
  border-radius: 8px; 
  background-color: #f9f9f9; 
}

.signup-container {
  width: 400px; 
  margin: 30px auto; 
  text-align: center; 
  padding: 20px; 
  border: 1px solid #ccc; 
  border-radius: 8px; 
  background-color: #f9f9f9; 
}

.b1{
  padding: auto;
}

.f1{
  display: flex;
  flex-direction: column;
  width:500px;
  padding: 20px;
  margin: auto;
}

.b1 {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  
}

.f1 {
  padding: 20px;
  background: white;
  border-radius: 5px;
  width: 300px;
}

.f1 input {
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.f1 input[type="submit"] {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.f1 input[type="submit"]:hover {
  background: #0056b3;
}



